<template>
    <div id="header" :class="{header_color:isScroll}">
        <el-container>
            <el-header>
                <el-row>
                    <el-col :span="4">
                    </el-col>
                    <el-col :span="20" class="nav-info">
                        <el-row>
                            <el-col :span="24">
                                <ul class="phone-ul">
                                    <li class="float-left phone-li">
                                        <img :src="phoneLogo" height="18px" width="18px">
                                        <span class="text-color2" :class="{nav_txt_color:isScroll}">(86) 0510-81015338</span>
                                    </li>
                                    <li class="float-left phone-li2">
                                        <a href="skype:live:royxcl?chat">Skype Contact</a>
                                        <a href="skype:henrybeas?chat" class="fa fa-skype"></a>
                                        <a href="https://www.facebook.com/hankbybay" class="fa fa-facebook"></a>
                                        <a href="https://twitter.com/@henryCynovo" class="fa fa-twitter"></a>
                                        <a href="https://www.instagram.com/alx7126/" class="fa fa-instagram"></a>
                                    </li>
                                    <li class="float-left phone-li2">
                                        <a @click="changeLang($event)">cn</a>
                                    </li>
                                </ul>
                            </el-col>
                            <el-col :span="24">
                                <ul class="nav-ul">
                                    <li class="float-left">
                                        <a href="#home" :class="{nav_txt_color:isScroll}"> {{ $t("nav_info").home }}</a>
                                    </li>
                                    <li class="float-left">
                                        <a href="#products" :class="{nav_txt_color:isScroll}"> {{ $t("nav_info").products }}</a>
                                    </li>
                                    <li class="float-left">
                                        <a href="#platform" :class="{nav_txt_color:isScroll}"> {{ $t("nav_info").platfrom }}</a>
                                    </li>
                                    <li class="float-left">
                                        <a href="#solutions" :class="{nav_txt_color:isScroll}"> {{ $t("nav_info").solutions }}</a>
                                    </li>
                                    <li class="float-left">
                                        <a href="#customers" :class="{nav_txt_color:isScroll}"> {{ $t("nav_info").customers }}</a>
                                    </li>
                                    <li class="float-left">
                                        <a href="#aboutUs" :class="{nav_txt_color:isScroll}"> {{ $t("nav_info").aboutus }}</a>
                                    </li>
                                </ul>
                            </el-col>
                        </el-row>
                    </el-col>
                </el-row>
            </el-header>
        </el-container>
    </div>
</template>
<script>
import logo from "@/img/logo.svg";
import phoneLogo from "@/img/phone.svg";
export default {
  name: "HelloWorld",
  data() {
    return {
      logo: logo,
      phoneLogo: phoneLogo,
      isScroll: false
    };
  },
  methods: {
    //切换语言
    changeLang(e) {
      let locale = this.$i18n.locale;
      let target = e.target;
      locale === "en" ? (this.$i18n.locale = "cn") : (this.$i18n.locale = "en");
      locale === "en" ? (target.innerHTML = "en") : (target.innerHTML = "cn");
    }
  },
  mounted() {
    window.addEventListener(
      "scroll",
      () => {
        this.$nextTick(function() {
          // 获取当前滚动条与窗体顶部的距离
          let distance =
            document.documentElement.scrollTop || document.body.scrollTop;
          distance > 0 ? (this.isScroll = true) : (this.isScroll = false);
        });
      },
      false
    );
  }
};
</script>
<style>
@import url(./header.less);
</style>



