<template>
    <section id="aboutUs" class="about-bg-color about-pad">
        <el-container class="container about-content">
            <div class="about-banner">
                <h2 class="text-color4e">{{$t("aboutUs_info").title}}</h2>
                <div class="about-banner">{{$t("aboutUs_info").msg}}</div>
            </div>
            <el-row>
                <el-col :span="8">
                    <img :src="bg" class="about-img">
                </el-col>
                <el-col :span="8" class="a-margin-top-30">
                    <h3>{{$t("aboutUs_info").column1.title}}</h3>
                    <div class="about-txt">
                        <p v-html="colum1Msg" style="text-align:justify;margin-bottom:20px;"></p>
                        <p>
                            <a href="https://v.qq.com/x/page/n0343zi02bn.html" target="_blank">{{$t('aboutUs_info').column1.vedioLink}}</a>
                        </p>
                        <p>
                            <br>
                        </p>
                    </div>
                </el-col>
                <el-col :span="8" class="a-margin-top-30">
                    <h3>{{$t("aboutUs_info").column2.title}}</h3>
                    <div class="about-container-h" style="overflow: hidden; height: 370px; margin-bottom: 12px;" ref="about_container_h">
                        <div class="about-txt about-txt-h" ref="about_txt_h">
                            <strong>{{$t("aboutUs_info").column2.year2018.year}}</strong>
                            <div class="mar-b-10">
                                <a href="https://pressreleasejet.com/news/cynovo-android-smart-pos-terminal-par6-passed-visa-and-mastercard-certifications.html#" target="_blank">{{$t("aboutUs_info").column2.year2018.msg}}</a>
                            </div>
                            <strong>{{$t("aboutUs_info").column2.year2017.year}}</strong>
                            <div class="mar-b-10">
                                {{$t("aboutUs_info").column2.year2017.msg}}
                            </div>
                            <strong>{{$t("aboutUs_info").column2.year2016.year}}</strong>
                            <div class="mar-b-10">
                                {{$t("aboutUs_info").column2.year2016.msg}}
                            </div>
                            <strong>{{$t("aboutUs_info").column2.year2015.year}}</strong>
                            <div class="mar-b-10">
                                {{$t("aboutUs_info").column2.year2015.msg}}
                            </div>
                            <strong>{{$t("aboutUs_info").column2.year2014.year}}</strong>
                            <transition name="fade" enter-active-class="fadeUp" leave-active-class="fadeDown">
                                <div v-if="isshow" class="mar-b-10 show">
                                    <p v-if="lang=='cn'" v-for='(item,index) in $t("aboutUs_info").column2.year2014.msg' :key="index" style="text-align:justify;">
                                        {{item}}
                                    </p>
                                    <p v-if="lang=='en'">
                                        {{$t("aboutUs_info").column2.year2014.msg}}
                                    </p>
                                </div>
                            </transition>
                        </div>
                    </div>
                    <div class="about-to-more">
                        <a @click="toggle($event);"> 
                          {{operation}}
                            <i class="fa fa-caret-down"></i>
                        </a>
                    </div>
                </el-col>
            </el-row>
        </el-container>
    </section>
</template>
<script>
import bg from "@/img/aboutUs/bg.jpg";
import "vue2-animate/dist/vue2-animate.min.css";
export default {
  data() {
    return {
      bg,
      isshow: false
    };
  },
  computed: {
    lang() {
      return this.$i18n.locale;
    },
    colum1Msg() {
      return this.$t("aboutUs_info").column1.msg;
    },
    operation() {
      return  this.$t("aboutUs_info").column2.operation[0];
    }
  },
  methods: {
    toggle(event) {
      let target = event.target;
      let lang = this.$i18n.locale;
      if (this.isshow) {
        if (lang == "en") {
          this.$refs.about_container_h.style.height = "500px";
        } else {
          this.$refs.about_container_h.style.height = "360px";
        }
        target.innerHTML = `${this.$t("aboutUs_info").column2.operation[0]}<i class="fa fa-caret-down"></i>`;
      } else {
        if (lang == "en") {
          this.$refs.about_container_h.style.height = "720px";
        } else {
          this.$refs.about_container_h.style.height = "600px";
        }
        target.innerHTML = `${this.$t("aboutUs_info").column2.operation[1]}<i class="fa fa-caret-up"></i>`;
      }
      this.isshow = !this.isshow;
    }
  }
};
</script>
<style>
@import url(./aboutUs.less);
</style>



