<template>
    <section id="enquire">
        <div class="enquire-width">
            <h1>{{$t("contact_info").title}}</h1>
            <el-container class="container">
                <el-row>
                    <el-col :span="12">
                        <h3 class="footer-Btitle">{{$t("contact_info").column1.title}}</h3>
                        <div class="contact-info">
                            <div>
                                <el-row>
                                    <el-col :span="4" class="contactInfo-title">
                                        <span>{{$t("contact_info").column1.address.title}}
                                            <span class="footer-fuhao">:</span>
                                        </span>
                                    </el-col>
                                    <el-col :span="20" class="contactInfo-txt mar-left">
                                        <span>{{$t("contact_info").column1.address.content}}</span>
                                    </el-col>
                                </el-row>
                            </div>
                            <div>
                                <el-row class="row contact-plr1">
                                    <el-col :span="4" class="contactInfo-title">
                                        <span class="mar-right footer-email">{{$t("contact_info").column1.email.title}}
                                            <span class="footer-fuhao2">:</span>
                                        </span>
                                    </el-col>
                                    <el-col :span="20" class="contactInfo-txt mar-left2">
                                        <span>{{$t("contact_info").column1.email.content[0]}}</span><br>
                                        <span>{{$t("contact_info").column1.email.content[1]}}</span>
                                    </el-col>
                                </el-row>
                            </div>
                            <div>
                                <el-row class="contact-plr2">
                                    <el-col :span="4" class="contactInfo-title">
                                        <span>{{$t("contact_info").column1.telPhone.title}}
                                            <span class="footer-fuhao3">:</span>
                                        </span>
                                    </el-col>
                                    <el-col :span="20" class="contactInfo-txt mar-left">
                                        <span>{{$t("contact_info").column1.telPhone.content}}</span>
                                    </el-col>
                                </el-row>
                            </div>
                            <div>
                                <el-row class="row contact-plr2">
                                    <el-col :span="4" class="contactInfo-title">
                                        <span>{{$t("contact_info").column1.fax.title}}
                                            <span class="footer-fuhao4">:</span>
                                        </span>
                                    </el-col>
                                    <el-col :span="20" class="contactInfo-txt mar-left">
                                        {{$t("contact_info").column1.fax.content}}
                                    </el-col>
                                </el-row>
                            </div>
                        </div>
                    </el-col>
                    <el-col :span="12">
                        <h3 class="form-title">{{$t("contact_info").column2.title}}</h3>
                        <el-form :model="contactForm" :rules="rules" ref="contactForm" class="contactForm">
                            <el-form-item required>
                                <el-col :span="12">
                                    <el-form-item prop="name">
                                        <el-input v-model="contactForm.name" :placeholder="forminfo.name"></el-input>
                                    </el-form-item>
                                </el-col>

                                <el-col :span="12">
                                    <el-form-item prop="email">
                                        <el-input v-model="contactForm.email" :placeholder="forminfo.email"></el-input>
                                    </el-form-item>
                                </el-col>
                            </el-form-item>
                            <el-form-item required>
                                <el-col :span="12">
                                    <el-form-item prop="country">
                                        <el-input v-model="contactForm.country" :placeholder="forminfo.country"></el-input>
                                    </el-form-item>
                                </el-col>

                                <el-col :span="12">
                                    <el-form-item prop="phone">
                                        <el-input v-model.number="contactForm.phone" :placeholder="forminfo.pehone"></el-input>
                                    </el-form-item>
                                </el-col>
                            </el-form-item>
                            <el-form-item required>
                                <el-col :span="12">
                                    <el-form-item prop="company">
                                        <el-input v-model="contactForm.company" :placeholder="forminfo.company"></el-input>
                                    </el-form-item>
                                </el-col>

                                <el-col :span="12">
                                    <el-form-item prop="theme">
                                        <el-input v-model="contactForm.theme" :placeholder="forminfo.theme"></el-input>
                                    </el-form-item>
                                </el-col>
                            </el-form-item>
                            <el-form-item prop="content">
                                <el-input type="textarea" v-model="contactForm.content" :placeholder="forminfo.content" style="min-height:64px;" class="content"></el-input>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="submitForm('contactForm')">{{forminfo.submit}}</el-button>
                            </el-form-item>
                        </el-form>
                    </el-col>
                </el-row>
            </el-container>
        </div>
    </section>
</template>
<script>
import util from "@/common/util";
export default {
  data() {
    return {
      contactForm: {
        name: "",
        email: "",
        country: "",
        phone: "",
        company: "",
        theme: "",
        content: ""
      },
      rules: {
        name: [
          { required: true, message: "name is required", trigger: "blur" }
        ],
        email: [
          { required: true, message: "name is required", trigger: "change" }
        ],
        country: [
          { required: true, message: "country is required", trigger: "blur" }
        ],
        phone: [
          { required: true, message: "phone is required", trigger: "blur" },
          {
            type: "number",
            message: "phone  must be numbers. ",
            trigger: "blur"
          }
        ],
        company: [
          { required: true, message: "company is required", trigger: "blur" }
        ],
        theme: [
          { required: true, message: "theme is required", trigger: "blur" }
        ],
        content: [
          { required: true, message: "content is content", trigger: "blur" }
        ]
      }
    };
  },
  computed: {
    forminfo() {
      return this.$t("contact_info").column2;
    }
  },
  methods: {
    submitForm(formName) {
      var _this = this;
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$fetch("/api/sendMail", _this.contactForm).then(res => {
            if (res.data.type === true) {
              util.success(res.data.data);
            } else {
              util.success(res.data.data);
            }
          });
        } else {
          return false;
        }
      });
    }
  }
};
</script>
<style>
@import url(./contactUs.less);
</style>



