<!--返回顶部组件-->
<template>
  <div id="toTop" @click="goTop" ref="toTop">
    <a href="#">
      <div class="fa fa-angle-up toTop-icon"></div>
      <div class="toTop-txt">To Top</div>
    </a>
  </div>
</template>
<script>
export default {
  data() {
    return {};
  },
  methods: {
    //返回顶部
    goTop() {
      //将滚动条置顶
      document.documentElement.scrollTop = 0;
      document.body.scrollTop = 0;
    }
  }
};
</script>
<style>
@import url(./operation.less);
</style>



