<template>
    <div id="home">
        <slider :pages="pages" :sliderinit="sliderinit">
            <!-- slot  -->
        </slider>
    </div>
</template>
<script>
import slider from "vue-concise-slider"; // 引入slider组件
import banner1_cn from "@/img/banner/banner1_cn.jpg";
import banner2_cn from "@/img/banner/banner2_cn.jpg";
import banner3_cn from "@/img/banner/banner3_cn.jpg";
import banner4_cn from "@/img/banner/banner4_cn.jpg";
import banner1_en from "@/img/banner/banner1_en.jpg";
import banner2_en from "@/img/banner/banner2_en.jpg";
import banner3_en from "@/img/banner/banner3_en.jpg";
import banner4_en from "@/img/banner/banner4_en.png";
export default {
  data() {
    return {
      //图片列表[arr]
      banners: [],
      //滑动配置[obj]
      sliderinit: {
        currentPage: 0, //当前页码
        thresholdDistance: 500, //滑动判定距离
        thresholdTime: 100, //滑动判定时间
        autoplay: 5000, //自动滚动[ms]
        loop: true, //循环滚动
        direction: "horizontal", //方向设置，垂直滚动
        infinite: 1, //无限滚动前后遍历数
        slidesToScroll: 1 //每次滑动项数
      }
    };
  },
  computed: {
    pages() {
      let banners = this.banners;
      if (this.$i18n.locale == "cn") {
        banners.push({
          html: `<div class="slider1"><img src='${banner1_cn}'></div>`
        });
        banners.push({
          html: `<div class="slider1"><img src='${banner2_cn}'></div>`
        });
        banners.push({
          html: `<div class="slider1"><img src='${banner3_cn}'></div>`
        });
        banners.push({
          html: `<div class="slider1"><img src='${banner4_cn}'></div>`
        });
      } else {
        banners.push({
          html: `<div class="slider1"><img src='${banner1_en}'></div>`
        });
        banners.push({
          html: `<div class="slider1"><img src='${banner2_en}'></div>`
        });
        banners.push({
          html: `<div class="slider1"><img src='${banner3_en}'></div>`
        });
        banners.push({
          html: `<div class="slider1"><img src='${banner4_en}'></div>`
        });
      }
      this.banners = [];
      return banners;
    }
  },
  components: {
    slider
  }
};
</script>
<style>
#home {
  min-height: 696px;
  max-height: 950px;
  margin-bottom: -20px;
}
.slider-container img {
  width: 100%;
}
</style>





