<!--解决方法组件-->
<template>
    <div>
        <!-- 引导页 -->
        <div>
            <div style="width:100%;height:100%">
                <img style="width:100%;height:100%" :src="guide_img">
            </div>
        </div>
        <!-- 引导页结束 -->
        <!--技术服务-->
        <div id="technology" class="technology-pad">
            <div class="technology-width">
                <h3>{{$t("serviceSupport_info").title}}</h3>
                <h4></h4>
                <div class="container">
                    <el-row>
                        <el-col :span="8">
                            <div class="technology-border">
                                <div class="technology-title">{{$t("serviceSupport_info").column1.title}}</div>
                                <img :src="tss_icon1">
                                <div class="technology-txt">{{$t("serviceSupport_info").column1.msg}}</div>
                            </div>
                        </el-col>
                        <el-col :span="8">
                            <div class="technology-border">
                                <div class="technology-title">{{$t("serviceSupport_info").column2.title}}</div>
                                <img :src="tss_icon2">
                                <div class="technology-txt">{{$t("serviceSupport_info").column2.msg}}</div>
                            </div>
                        </el-col>
                        <el-col :span="8">
                            <div class="technology-border">
                                <div class="technology-title">{{$t("serviceSupport_info").column3.title}}</div>
                                <img :src="tss_icon3">
                                <div class="technology-txt">
                                    <p>
                                        {{$t("serviceSupport_info").column3.msg}}
                                    </p>
                                </div>
                            </div>
                        </el-col>
                    </el-row>
                </div>
            </div>
        </div>
        <!--技术服务结束-->
        <!--轮播图-->
        <div>
            <slider :pages="pages" :sliderinit="sliderinit">
                <!-- slot  -->
            </slider>
        </div>
        <!--轮播图结束-->
        <!--解决方案-->
        <div id="solutions" class="solutions-pad">
            <div class="solutions-width">

                <h3>{{$t("solutions_info").title}}</h3>
                <!-- <h4>解决行业关键问题</h4> -->
                <div class="container">
                    <el-row>
                        <el-col :span="8">
                            <div class="solutions-border">
                                <img :src="solutions_icons[0]">
                                <div class="solution-txt">{{$t("solutions_info").column1.msg}}</div>
                            </div>
                            <div class="solutions-title">{{$t("solutions_info").column1.title}}</div>
                        </el-col>
                        <el-col :span="8">
                            <div class="solutions-border">
                                <img :src="solutions_icons[1]">
                                <div class="solution-txt">{{$t("solutions_info").column2.msg}}</div>
                            </div>
                            <div class="solutions-title">{{$t("solutions_info").column2.title}}</div>
                        </el-col>
                        <el-col :span="8">
                            <div class="solutions-border">
                                <img :src="solutions_icons[2]">
                                <div class="solution-txt">{{$t("solutions_info").column3.msg}}</div>
                            </div>
                            <div class="solutions-title">{{$t("solutions_info").column3.title}}</div>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8">
                            <div class="solutions-border">
                                <img :src="solutions_icons[3]">
                                <div class="solution-txt">{{$t("solutions_info").column4.msg}}</div>
                            </div>
                            <div class="solutions-title">{{$t("solutions_info").column4.title}}</div>
                        </el-col>
                        <el-col :span="8">
                            <div class="solutions-border">
                                <img :src="solutions_icons[4]">
                                <div class="solution-txt">{{$t("solutions_info").column5.msg}}</div>
                            </div>
                            <div class="solutions-title">{{$t("solutions_info").column5.title}}</div>
                        </el-col>
                        <el-col :span="8">
                            <div class="solutions-border">
                                <img :src="solutions_icons[5]">
                                <div class="solution-txt">{{$t("solutions_info").column6.msg}}</div>
                            </div>
                            <div class="solutions-title">{{$t("solutions_info").column6.title}}</div>
                        </el-col>
                    </el-row>
                </div>
            </div>
        </div>
        <!--解决方案结束-->
    </div>

</template>
<script>
import slider from "vue-concise-slider"; // 引入slider组件
import guide_img_cn from "@/img/solutions/guide_img_cn.png";
import guide_img_en from "@/img/solutions/guide_img_en.png";
import tss_icon1 from "@/img/solutions/tss_icon1.png";
import tss_icon2 from "@/img/solutions/tss_icon2.png";
import tss_icon3 from "@/img/solutions/tss_icon3.png";
import banner1_cn from "@/img/solutions/banner1_cn.png";
import banner2_cn from "@/img/solutions/banner2_cn.png";
import banner3_cn from "@/img/solutions/banner3_cn.png";
import banner1_en from "@/img/solutions/banner1_en.png";
import banner2_en from "@/img/solutions/banner2_en.png";
import banner3_en from "@/img/solutions/banner3_en.png";
import solutions_icon1 from "@/img/solutions/solutions_icon1.png";
import solutions_icon2 from "@/img/solutions/solutions_icon2.png";
import solutions_icon3 from "@/img/solutions/solutions_icon3.png";
import solutions_icon4 from "@/img/solutions/solutions_icon4.png";
import solutions_icon5 from "@/img/solutions/solutions_icon5.png";
import solutions_icon6 from "@/img/solutions/solutions_icon6.png";
export default {
  data() {
    return {
      //图片列表[arr]
      banners: [],
      //滑动配置[obj]
      sliderinit: {
        currentPage: 0, //当前页码
        thresholdDistance: 500, //滑动判定距离
        thresholdTime: 100, //滑动判定时间
        autoplay: 5000, //自动滚动[ms]
        loop: true, //循环滚动
        direction: "horizontal", //方向设置，垂直滚动
        infinite: 1, //无限滚动前后遍历数
        slidesToScroll: 1 //每次滑动项数
      },
      guide_img_cn,
      guide_img_en,
      tss_icon1,
      tss_icon2,
      tss_icon3,
      solutions_icons: [
        solutions_icon1,
        solutions_icon2,
        solutions_icon3,
        solutions_icon4,
        solutions_icon5,
        solutions_icon6
      ]
    };
  },
  computed: {
    guide_img() {
      return this.$i18n.locale == "en" ? guide_img_en : guide_img_cn;
    },
    pages() {
      let banners = this.banners;
      if (this.$i18n.locale == "cn") {
        banners.push({
          html: `<div class="slider1"><img src='${banner1_cn}'></div>`
        });
        banners.push({
          html: `<div class="slider1"><img src='${banner2_cn}'></div>`
        });
        banners.push({
          html: `<div class="slider1"><img src='${banner3_cn}'></div>`
        });
      } else {
        banners.push({
          html: `<div class="slider1"><img src='${banner1_en}'></div>`
        });
        banners.push({
          html: `<div class="slider1"><img src='${banner2_en}'></div>`
        });
        banners.push({
          html: `<div class="slider1"><img src='${banner3_en}'></div>`
        });
      }
      this.banners = [];
      return banners;
    }
  },
  components: {
    slider
  }
};
</script>
<style>
@import url(./solutions.less);
</style>





