<template>
    <section class="index-icon">
        <div class="index-iconWidth">
            <h1>{{$t("certification_info").title}}</h1>
            <div class="container icon-img">
                <el-row>
                    <el-col :span="4" v-for="(item,index) in certification_icons" :key="index">
                        <img :src="item">
                    </el-col>
                </el-row>
            </div>
        </div>
    </section>
</template>
<script>
import certification_icon1 from "@/img/platform/certification_icon1.png";
import certification_icon2 from "@/img/platform/certification_icon2.png";
import certification_icon3 from "@/img/platform/certification_icon3.png";
import certification_icon4 from "@/img/platform/certification_icon4.png";
import certification_icon5 from "@/img/platform/certification_icon5.png";
import certification_icon6 from "@/img/platform/certification_icon6.png";
export default {
  data() {
    return {
      certification_icons: [
        certification_icon1,
        certification_icon2,
        certification_icon3,
        certification_icon4,
        certification_icon5,
        certification_icon6
      ]
    };
  }
};
</script>
<style>
@import url(./certification.less);
</style>






