<template>
    <section id="customers" class="customers-pad">
        <div class="customers-width">
            <h1>{{$t("customers_info").title}}</h1>
            <el-container class="container customers-img">
                <el-row>
                    <el-col :span="6">
                        <img :src="icons[0]" />
                    </el-col>
                    <el-col :span="6">
                        <img :src="icons[1]" />
                    </el-col>
                    <el-col :span="6">
                        <img :src="icons[2]" />
                    </el-col>
                    <el-col :span="6">
                        <img :src="icons[3]" />
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="6">
                        <img :src="icons[4]" />
                    </el-col>
                    <el-col :span="6">
                        <img :src="icons[5]" />
                    </el-col>
                    <el-col :span="6">
                        <img :src="icons[6]" />
                    </el-col>
                    <el-col :span="6">
                        <img :src="icons[7]" />
                    </el-col>
                </el-row>
            </el-container>
        </div>
    </section>
</template>
<script>
import icon1 from "@/img/customers/icon1.png";
import icon2 from "@/img/customers/icon2.png";
import icon3 from "@/img/customers/icon3.png";
import icon4 from "@/img/customers/icon4.png";
import icon5 from "@/img/customers/icon5.png";
import icon6 from "@/img/customers/icon6.png";
import icon7 from "@/img/customers/icon7.png";
import icon8 from "@/img/customers/icon8.png";
export default {
  data() {
    return {
      icons: [icon1, icon2, icon3, icon4, icon5, icon6, icon7, icon8]
    };
  }
};
</script>
<style>
@import url(./customers.less);
</style>



