import config from '@/config/config.js'
let util = {};
// 控制台输出
util.console = function(msg) {
    console.log(msg);
};
//去除空格
util.trim = function(str) {
        return str.replace(/^\s+|\s+$/gm, '');
    }
    // 成功
util.success = (val = "成功！") => {
    layui.use('layer', function() {
        console.log(layer);
        layer.msg(val, { icon: 1, offset: config.layer.msg.top, time: config.layer.msg_timeout });
    });
};
// 失败
util.error = (val = "失败！") => {
    layui.use('layer', function() {
        layer.msg(val, { icon: 2, offset: config.layer.msg.top, time: config.layer.msg_timeout });
    });
};
// 文件
util.saveExcelFile = function(data) {
    var fileName = data.fileName;
    // 判断空
    if (util.isEmpty(fileName)) return;

    var bin = atob(data.base64.replace(/\s/g, ''));
    var buffer = new Uint8Array(bin.length);
    for (var i = 0; i < bin.length; i++) {
        buffer[i] = bin.charCodeAt(i);
    }

    var blob = new Blob([buffer.buffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });

    // 输出文件
    if (window.navigator.msSaveBlob) {
        window.navigator.msSaveBlob(blob, fileName);
    } else {
        var a = document.createElement('a');
        a.href = URL.createObjectURL(blob);
        a.target = '_blank';
        a.download = fileName;
        a.click();
        URL.revokeObjectURL(a.href);
    }
};
// 判断对象是否为空
util.isEmpty = function(str) {
    if (str === '' || str === undefined || str === null) {
        return true;
    }
    return false;
};
// 字符串包含
util.isContains = function(str, substr) {
        return str.indexOf(substr) >= 0;
    }
    // 格式化时间
util.formatTime = function(date) {
    var year = date.getFullYear();
    var month = date.getMonth() + 1;
    var day = date.getDate();
    var hour = date.getHours();
    var minute = date.getMinutes();;
    var second = date.getSeconds();

    return [year, month, day].map(formatNumber).join('-') + ' ' + [hour, minute, second].map(formatNumber).join(':')
};
export default util;


